@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Oswald:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  padding: 0;
  margin: 0;
  text-decoration: none;
  /* box-sizing: border-box; */
}

h1, h2, h3, button, .active {
  font-family: 'Oswald', sans-serif;
  font-weight: 700;
}

p, a, li {
  font-family: 'Montserrat', sans-serif;
}

img {
  max-width: 100%;
}

.container {
  width: 80%;
  max-width: 1100px;
  margin: 0 auto;
  padding: 5em 0 ;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.article{
 background-color: #cdcdcd;
 color: #2E323f;
 padding: 2em 0;
}

.article__header {
  font-size: 2rem;
  color: #a59678;
}

.article__text {
  text-align: center;
  font-size: .9rem;
  margin: 20px;
}

.article--grid {
  margin: 100px auto;
}

.article__one--text {
  width: 60%;
}

.article__one--text h2 {
  color: #a59678;
  font-size: 2.25rem;
}

.article__one--text p {
  line-height: 1.8;
  font-size: 1rem;
}

.article__two--text {
  width: 40%;
  align-items: flex-start;
  margin-left: 50px;
}

.article__two--text h2 {
  color: #ffffff;
  font-size: 1.8rem;
}

.article__two--text p {
  color: #cdcdcd;
  font-size: .9rem;
}

.grid_article__text {
  background-color: #2E323f;
  text-align: center;
  padding: 30px;
  margin: 10px;
}

footer {
  background-color: #3b4050;
  padding: 2em 0;
}

footer h2 {
  color: #a59678;
  font-size: 1.5rem;
}

footer p, a {
  color: #cdcdcd;
  line-height: 2.0;
  font-size: 1rem;
}

footer a {
  text-decoration: none;
}

footer a:hover {
  color: #ffffff;
}

footer li {
  list-style: none;
  
}

.footer_items {
  padding: 50px 0;
}

.footer-header {
  color: #ffffff;
  text-align: center;
  font-size: 3rem;
}

.about__footer {
  width: 40%;
}

@media screen and (max-width: 800px) {
  .container {
    width: 80%;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
  }

  .row {
    display: block;
    width: 80%;
  }

  .article__text {
    margin-left: auto;
    margin-right: auto;
  }

  .article__text p {
    width: 70%;
    margin: 0 auto;
  }

  .article__one--text, .article__two--text {
    width: 80%;
    margin: 0 auto;
    text-align: center;
  }

  .article__two--text {
    margin-left: -40px;

  }

  .grid_article__text {
    display: block;
    width: 80%;
  }

  .about__footer {
    width: 80%;
    margin: 0 auto;
  }

  .footer__links {
    margin-left: -40px;
  }
}
.navbar {
    background: #2E323f;
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    display: flex;
    justify-content: space-between;
    height: 80px;
  }
  
  .container {
    z-index: 1;
    width: 100%;
    max-width: 1100px;
    margin-right: auto;
    margin-left: auto;
    /* padding-right: 50px;
    padding-left: 50px; */
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  
  .navbar-icon {
    margin-right: 0.5rem;
  }
  
  .nav-menu {
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    text-align: center;
    justify-content: end;
  }
  
  .nav-item {
    height: 80px;
    border-bottom: 2px solid transparent;
  }
  
  .nav-item:hover {
    border-bottom: 2px solid #f00946;
  }
  
  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .menu-icon {
    display: none;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #1c2237;
      left: 0;
      top: 60px;
      opacity: 1;
      transition: all 0.6s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      /* display: table; */
    }
  
    .nav-links:hover {
      color: #f00946;
      transform: scale(1.2);
      transition: all 0.3s ease;
    }
  
    .nav-item:hover {
      border: none;
    }
  
    .nav-item {
      width: 100%;
    }
  
    .navbar-logo {
      position: absolute;
      top: -25px;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 120px;
    }
  }
.main {
    background-color: #3b4050;
    color: #ffffff;
    margin: 0;
    top: 0;
}

.main__content {
    width: 50%;
    padding: 2em 0;
}
  
.big__text {
    font-size: 3rem;
}
  
.gold__text {
    color: #a59678;
    margin-left: 20px;
}
  
.small__text {
    font-size: 1.35rem;
    line-height: 1.7;
}
  
.btn__one {
    background-color: #a59678;
    border-radius: 100px;
    padding: .3em .95em;
    color: #ffffff;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
}

  
@media screen and (max-width: 960px) {
    .big__text {
        font-size: 1.5rem;
        width: 70%;
    }
    .small__text {
        font-size: 1rem;
        width: 70%;
    }
    .main__content {
        width: 100%;
    }
}

@media screen and (max-width: 550px) {
    .big__text {
        width: 70%;
    }
    .small__text {
        width: 80%;
    }
}
